import React from 'react';
import ReactDOM from 'react-dom';
import './App.scss';
import styles from './mystyle.module.scss';

class Car extends React.Component {
  constructor(props) {
    super(props) ;
    this.state = {
      brand: "Ford",
      model: "Mustang",
      color: "red",
      year: 1964,
      favouritecolor: "pink"
    };
  }
  changeColor = () => {
    this.setState({favouritecolor: "blue"});
  }
  changeToOrange = () => {
    alert("Wow, thats a great Click!");
    this.setState({favouritecolor: "orange"});
  }
  changeToColor = (color) => {
    this.setState({color: color});
  }
  thisColor = (color) => {
    this.setState({color: color.target.value});
  }
  render() {
    const buttonstyle = {
      color: "white",
      backgroundColor: "DodgerBlue",
      padding: "10px",
      fontFamily: "Arial",
      letterSpacing: "1.2px",
      borderRadius: "4px",
      fontSize: "12px",
      padding: "8px 12px",
      textTransform: "uppercase",
      marginRight: "4px",
      marginBottom: "24px",
      fontWeight: "900",
      outline: "none"
    };
    return (
      <div>
        <h1 className={styles.bigblue}>Hey hey, this is a car. My name is {this.state.brand} {this.state.model}</h1>
        <p>
          I am a {this.state.color} {this.state.model} from {this.state.year}.
          This is a recent picture of me my mom took in Mexico.
        </p>

        <div style={{ width: "50px", height: "40px", background: this.state.color, float: "left", marginTop: "35px" }}></div>
        <div style={{ width: "100px", height: "75px", background: this.state.color , float: "left" }}></div>
        <div style={{ width: "50px", height: "40px", background: this.state.color, float: "left", marginTop: "35px"  }}></div>


        <h2 style={{ marginTop: "140px" }}>Btw, my favourite color is {this.state.favouritecolor}. But its changing.</h2>

        <button style={buttonstyle} type="button" onClick={this.changeColor}>Change {this.state.brand}´s fav color</button>
        <button style={buttonstyle} type="button" onClick={this.changeToOrange}>Change color to orange</button>
        <button style={buttonstyle} type="button" onClick={() => this.changeToColor("black")}>Change {this.state.brand}´s color to black</button>
        <button style={buttonstyle} type="button" onClick={this.changeToColor.bind(this, "purple")}>Change {this.state.brand}´s color to purple</button>
        <form>

          I am not interested in any of the suggested color. I think {this.state.brand} {this.state.model} would look the best in color:
          <input
            type="text"
            onChange={this.thisColor}
          />
        </form>
      </div>
    );
  }
  componentDidMount() {
  setInterval(() => {
    this.setState({color: "yellow"})
  }, 4000)
}
}

class Garage extends React.Component {
  render() {
    const carinfo = {name: "Ford", model: "Mustang"};
    return (
      <div>
        <h1>Hey this is a garage. Car, are you there?</h1>
        <Car brand={carinfo} />
        <MyForm />

      </div>
    );
  }
}

class MyForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username: '',
      message: ''
    };
  }
  myChangeHandler = (event) => {
    this.setState({username: event.target.value});
  }
  destiny = (message) => {
    this.setState({message: message});
  }
  mySubmitHandler = (event) => {
    event.preventDefault();
    alert("Thank you for your submission, " + this.state.username + "we will now evaluate your answer and send you feedback");
  }
  render() {
    let header = '';
    if (this.state.username) {
      header = <h1>Hello {this.state.username}! {this.state.message}</h1>
    } else {
      header = '';
    }
    return (
      <form onSubmit={this.mySubmitHandler}>
        {header}
        <p>please fill in our form with your data and send it to be processed in our superprocessor and find out what are you</p>
        <p>Enter your name:</p>
        <input
          type="text"
          onChange={this.myChangeHandler}
        />
        <button type="button" onClick={this.destiny.bind(this, "You are so beautiful!")}>Send data</button>
        <input
          type='submit'
        />
      </form>
    );
  }
}

export default Garage;
